// react imports
import React, { Component } from 'react';
import { Link } from 'react-router-dom';

//images
import logo from '../../static/images/logo.png';

// css
import '../../static/styles/components/BasePage/NavBarWeb.css';

//urls
import { REGISTER_DET, link, msg, GOOGLE_REG_FORM } from '../../static/utils/applicationUrls';
import { NavBarWebPage,getIP, sendDataTOServer } from '../../static/utils/pageNames';

export default class NavBarWeb extends Component {
	constructor(props) {
		super(props);
		this.state = {
			prevScrollpos: window.pageYOffset,
			visible: true,
		};
	}

	// Adds an event listener when the component is mount.
	async componentDidMount() {
		window.addEventListener('scroll', this.handleScroll);
	}
	
	async makeRequest() {
		const get_ip = await getIP()
		const data = {"ip":get_ip, "page":NavBarWebPage}
		console.log("Navbarweb", data)
		return await sendDataTOServer(data)
	}

	// Remove the event listener when the component is unmount.
	componentWillUnmount() {
		window.removeEventListener('scroll', this.handleScroll);
	}

	// Hide or show the menu.
	handleScroll = () => {
		const { prevScrollpos } = this.state;
		const currentScrollPos = window.pageYOffset;
		const visible = prevScrollpos > currentScrollPos;
		this.setState({
			prevScrollpos: currentScrollPos,
			visible,
		});
	};

	render() {
		return (
			<div className='WebNavBarHideInMobile'>
				<div className={this.state.visible ? 'WebNavbar' : 'navhide'}>
					<div
						className='WebNavbar__logo'
						onClick={(event) => {
							event.preventDefault();
							window.location.href = '/';
						}}>
						<div className='WebNavbar__logo--set'>
							<img className='logo' src={logo} alt='logo'></img>
						</div>
					</div>
					<div className='WebNavbar__menu'>
						<Link to='/'>
							<div className='WebNavbar__menu--items'>
								Home
							</div>
						</Link>
						<Link to='/career'>
							<div className='WebNavbar__menu--items'>
								Career Clubs
							</div>
						</Link>
						<Link to='/personality'>
							<div className='WebNavbar__menu--items'>
								Personality Clubs
							</div>
						</Link>
						<Link to='/community'>
							<div className='WebNavbar__menu--items'>
								Community
							</div>
						</Link>
						<a
							href={GOOGLE_REG_FORM}
							className='WebNavbar__menu--items'
							target='_blank' rel="noreferrer">
							<button className='btn--ticket--golden'
							onClick={this.makeRequest}
								>
								Apply for Free
							</button>
						</a>
					</div>
				</div>
			</div>
		);
	}
}
