// react
import React, { useState } from 'react';
//images
import Background from '../../static/images/Backgrounds/start-journey-bg.svg';

// css
import '../../static/styles/components/BasePage/PredictMonths.css';

export default function PredictMonths() {

    const [months, setMonths] = useState("6");

    function setPredictMonths(value) {
        console.log(value);
        if (value == 0)
            monthsPredict = "40+"
        else {
            var hours = value / 20
            var monthsPredict_ = 500 / (30 * hours) 
            var monthsPredict = Math.ceil(monthsPredict_)
        }
        setMonths(monthsPredict)
    }

    return (
        <>
            <div className='predict-page'>
                <div className='predict-head'>
                    <div className='journey-heading'>
                        Let's predict the no. of
                        <span className='orange-text'> months </span>
                        you will take.
                    </div>
                    <p className='predict-title'>Select the productive hours you can give daily </p>
                    <div className='range-slider'>
                        <input id="range_value" onChange={e => setPredictMonths(e.target.value)} type="range" min="0" max="120" defaultValue="60" step="20" />
                        <div className='range-values'>
                            <h4>0hr</h4>
                            <h4>1hr</h4>
                            <h4>2hr</h4>
                            <h4>3hr</h4>
                            <h4>4hr</h4>
                            <h4>5hr</h4>
                            <h4>6hr</h4>
                        </div>
                    </div>
                    <p className='range-output'>Wondering how you'll achieve <span className='orange-text' id='timeline'> {months} Months </span> of Consistency?</p>
                </div>
                <img
                    src={Background}
                    className='journey-bg'
                    alt='experience'
                />
            </div>
        </>
    );
}
