// react
import React from 'react';

//images
import Plane from '../../static/images/Icons/plane_in_button.png';
import Background from '../../static/images/Backgrounds/start-journey-bg.svg';
import JourneySteps from '../../static/images/Icons/journey-steps.svg';

// css
import '../../static/styles/components/BasePage/StartJourney.css';

//urls
import {
	GOOGLE_REG_FORM
} from '../../static/utils/applicationUrls';
import { StartJourneyPage, getIP, sendDataTOServer } from '../../static/utils/pageNames';
export default function StartJourney()  {
   
    async function makeRequest() {
            const get_ip = await getIP()
            const data = {"ip":get_ip, "page":StartJourneyPage}
            console.log("Start Joruney", data)
            return await sendDataTOServer(data)
    }
    

    return (
        <>
            <div className='journey-page'>
                <div className='journey-head'>
                    <div className='journey-heading'>
                        Ready to
                        <span className='orange-text'> Start </span>
                        your Journey
                    </div>
                    <a href={GOOGLE_REG_FORM} target='_blank' rel="noreferrer">
						<button className='button sidebtn' onClick = {() => makeRequest()}>
							Apply for Free
							<img
								alt='plane-icon'
								src={Plane}
								className='PlaneInButton'
							/>
						</button>
					</a>
                </div>
                <img src={JourneySteps} className='journey-steps' alt='steps' />
                <img
                    src={Background}
                    className='journey-bg '
                    alt='experience'
                />
            </div>
        </>
    );
}
